.cabinet {
  margin-bottom: 70px;

  @include min-width(lg) {
    margin-bottom: 85px;
  }

  &__title {
    @include min-width(lg) {
      margin-bottom: 55px;
    }

  }

  &__wrap {
    @include min-width(lg) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__content {
    @include min-width(lg) {
      width: 275px;
    }
  }

  &__text {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 24px;

    @include min-width(lg) {
      margin-bottom: 35px;
      font-size: 24px;
      line-height: 28px;
    }
  }

  &__list {
    margin: 0;
    padding-left: 20px;
    margin-bottom: 40px;
  }

  &__image {
    width: 100%;
    filter: drop-shadow(0px 4px 4px rgba(15, 15, 22, 0.25)) drop-shadow(0px 15px 36px #0F0F16);
    border-radius: 16px;

    @include min-width(lg) {
      width: 735px;
    }
  }
}
